import React, { Component } from "react"
import { Link } from "gatsby"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { connect } from "react-redux";
import { authActions } from '../redux/actions/';

import { Title, Field, Label, Control, Input, Button, Columns, Column } from 'bloomer';

class UserFormPage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      data: {
        email: '',
        password: ''
      }
    }

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(e) {
    const { data } = this.state
    this.setState({
      data: {
        ...data,
        [e.target.name]: e.target.value
      }
    })
  }

  handleSubmit(e) {
    e.preventDefault()
    let data = this.state.data
    this.props.dispatch(authActions.login(data))
  }

  render() {
    const { data } = this.state

    return (
      <Layout>
        <SEO title="Login Page"/>
        <section class="section hero">
          <div class="hero-body">
            <div class="container">
              <div class="columns is-centered">
                <div class="column is-two-fifths">
                  <div class="card">
                    <div class="card-content">
                      <form
                        method="post"
                        onSubmit={e => {
                          this.handleSubmit(e)
                        }}
                      >
                        <div class="field">
                          <label class="label">E-mail Address</label>
                          <div class="control is-clearfix">
                            <Input
                            type="text"
                            name="email"
                            value={data.email}
                            onChange={this.handleChange}
                            placeholder='Email Address' />
                          </div>
                        </div>
                        <div class="field">
                          <label class="label">Password</label>
                          <div class="control is-clearfix">
                            <Input
                            type="password"
                            name="password"
                            value={data.password}
                            onChange={this.handleChange}
                            placeholder='Password' />
                          </div>
                        </div>
                        <hr/>
                        <div class="field is-grouped">
                          <div class="control">
                            <Button isColor='primary' type='submit'>Login</Button>
                          </div>
                          <div class="control">
                            <Link to="/user" className="button is-link">Back</Link>
                          </div>
                        </div>
                      </form>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </Layout>
    )
  }

}

const mapStateToProps = ({ user }) => {
  return { user }
}

export default connect(mapStateToProps)(UserFormPage);
